const module = {
  config: {
    name: 'master-data-HRM',
    svg: './static/img/icon/master.svg',
    desc: 'Modul ini digunakan untuk menympan data master status kerja sistem HRM V2. Master data ini nantinya digunakan untuk sub modul Jabatan, penetuan kode akun penggajian, dll',
    layout: true
  },
  routes: [
    { name: 'master-divisi', icon: 'ri-community-line', path: '/divisions', component: () => import('./divisions.vue') },
    { name: 'kategori-jabatan', icon: 'ri-briefcase-4-line', path: '/master-job-position-categories', component: () => import('./master-job-position-categories.vue') },
    { name: 'daftar-jabatan', icon: 'ri-briefcase-4-line', path: '/job-positions', component: () => import('./job-positions.vue') },
    { name: 'jenis-dokumen', icon: 'ri-dashboard-line', path: '/master-document-types', component: () => import('./master-document-types.vue') },
    { name: 'jenis-inventaris-aset', icon: 'ri-dashboard-line', path: '/master-inventaris-asset-types', component: () => import('./master-inventaris-asset-types.vue') }
  ]
}
export default module



