const module = {
  config: {
    name: 'ketenagakerjaan',
    svg: './static/img/icon/pengelolaan.svg',
    desc: 'Modul ini digunakan untuk menympan data master status kerja sistem HRM V2. Master data ini nantinya digunakan untuk sub modul Jabatan, penetuan kode akun penggajian, dll',
    layout: true
  },
  routes: [
    { name: 'tenaga-kerja', icon: 'ri-user-2-line', path: '/employees', component: () => import('./employees.vue') },
    { name: 'daftar-sanksi', icon: 'ri-user-2-line', path: '/employee-sanksi', component: () => import('./employee-sanksi.vue') },
    { separator: 'Event & Announcements', name: 'lowongan-pekerjaan', icon: 'ri-user-2-line', path: '/lowongan', component: () => import('./lowongan.vue') },
    { separator: 'Event & Announcements', name: 'event-perusahaan', icon: 'ri-bookmark-3-line', path: '/events', component: () => import('./events.vue') },
    { separator: 'Event & Announcements', name: 'announcements', icon: 'ri-slideshow-3-line', path: '/announcements', component: () => import('./announcements.vue') },
    { separator: 'Presensi', name: 'rekap-presensi', icon: 'ri-user-settings-line', path: '/presence-on-periodes', component: () => import('./presence-on-periodes.vue') },
    { separator: 'Presensi', name: 'rekap-lembur', icon: 'ri-user-settings-line', path: '/lembur-on-periodes', component: () => import('./lembur-on-periodes.vue') },
    { separator: 'Master Data', name: 'master-tipe-dokumen', icon: 'ri-book-mark-line', path: '/master-employee-document-types', component: () => import('./master/master-employee-document-types.vue') },
    { separator: 'Master Data', name: 'master-pendidikan', icon: 'ri-book-mark-line', path: '/master-educations', component: () => import('./master/master-educations.vue') },
    { separator: 'Master Data', name: 'status-kerja', icon: 'ri-shield-user-line', path: '/master-status-employments', component: () => import('./master/master-status-employments.vue') },
    { separator: 'Master Data', name: 'hubungan-keluarga', icon: 'ri-group-2-line', path: '/master-family-relationships', component: () => import('./master/master-family-relationships.vue') },
    { separator: 'Master Data', name: 'jenis-PHK', icon: 'ri-history-line', path: '/master-laid-off-types', component: () => import('./master/master-laid-off-types.vue') },
    { separator: 'Master Data', name: 'komponen-sanksi', icon: 'ri-history-line', path: '/master-sanksi-types', component: () => import('./master/master-sanksi-types.vue') }
  ]
}
export default module



