const module = {
  config: {
    name: 'perusahaan',
    svg: './static/img/icon/perusahaan.svg',
    desc: 'Modul ini digunakan untuk menympan data master status kerja sistem HRM V2. Master data ini nantinya digunakan untuk sub modul Jabatan, penetuan kode akun penggajian, dll',
    layout: true
  },
  routes: [
    { name: 'perusahaan', icon: 'ri-global-line', path: '/company', component: () => import('./company.vue') },
    { hidden: true, name: 'struktur-organisasi', icon: 'ri-organization-chart', path: '/company-organization-structures', component: () => import('./company-organization-structures.vue') },
    { hidden: true, name: 'organisasi', icon: 'ri-organization-chart', path: '/companies', component: () => import('./companies.vue') }
  ]
}
export default module



