const module = {
  config: {
    name: 'BPJS',
    svg: './static/img/icon/bpjs.svg',
    desc: 'Modul ini digunakan untuk menympan data master status kerja sistem HRM V2. Master data ini nantinya digunakan untuk sub modul Jabatan, penetuan kode akun penggajian, dll',
    layout: true
  },
  routes: [
    { name: 'iuran-BPJS-kesehatan', icon: 'ri-hospital-line', path: '/bpjs-kes-bills', component: () => import('./bpjs-kes-bills.vue') },
    { name: 'iuran-BPJS-tenaga-kerja', icon: 'ri-building-line', path: '/bpjs-tk-bills', component: () => import('./bpjs-tk-bills.vue') },
    { separator: 'Premi Bulanan', name: 'premi-BPJS-kesehatan', icon: 'ri-heart-pulse-line', path: '/config-prosen-bpjs-kesehatan', component: () => import('./config-prosen-bpjs-kesehatan.vue') },
    { separator: 'Premi Bulanan', name: 'premi-BPJS-tenaga-kerja', icon: 'ri-briefcase-4-line', path: '/config-prosen-bpjs-tenaga-kerja', component: () => import('./config-prosen-bpjs-tenaga-kerja.vue') },
    { separator: 'Master Data', name: 'keluarga-BPSJ-kesehatan', icon: 'ri-building-line', path: '/bpjs-kes-tambahan-employee-families', component: () => import('./bpjs-kes-tambahan-employee-families.vue') },
    { separator: 'Master Data', name: 'mapping-upah-BPJS', icon: 'ri-mind-map', path: '/mapping-upah-bpjs-list', component: () => import('./mapping-upah-bpjs-list.vue') }
  ]
}
export default module



