const module = {
  config: {
    name: 'koperasi',
    svg: './static/img/icon/koperasi.svg',
    desc: 'Modul ini digunakan untuk menympan data master status kerja sistem HRM V2. Master data ini nantinya digunakan untuk sub modul Jabatan, penetuan kode akun penggajian, dll',
    layout: true
  },
  routes: [
    { name: 'anggota-koperasi', icon: 'ri-group-line', path: '/koperasi-members', component: () => import('./koperasi-members.vue') },
    { name: 'simpan-pinjam', icon: 'ri-exchange-line', path: '/koperasi-simpan-pinjam', component: () => import('./koperasi-simpan-pinjam.vue') },
    { separator: 'Master Data', name: 'komponen-simpanan', icon: 'ri-save-line', path: '/master-comp-simpanan-koperasi', component: () => import('./master/master-comp-simpanan-koperasi.vue') }
  ]
}
export default module



